<template>
  <muse-main-form style="width:240px; height:320px">
    <muse-page v-for="page in showPages" :key="page" :pagenode="page" @show-page-event="changePage"/>

    <h1 style="position: absolute; top: 340px;">{{hello}}</h1>
    <input type="file" id="file-selector" style="position: absolute; top: 400px;" accept=".kaml" @change="onLoadKaml"/>
  </muse-main-form>
</template>

<script src="node_modules/jquery/dist/jquery.js"></script>
<script>
import MuseMainForm from '@/ui/MuseMainForm.vue'
import MusePage from '@/ui/MusePage.vue'
export default {
  name: 'App',

  components: {
    MusePage
  },

  created() {
    this.hello = "11aa";
    
    var $ = require('jquery');
    var xmlHttp = new XMLHttpRequest();

    var me = this;
    xmlHttp.onreadystatechange = function() {
        if(xmlHttp.readyState == 4) {
            if(xmlHttp.status == 200) {
                //var knbase = $("#main");
                var xml = xmlHttp.responseXML.documentElement;
                //me.setKamlLayput(me, docelem);

                me.hello = "XML!"
                me.SerializeRoot(xml);
            }
        }
    }
    xmlHttp.open("GET", "https://kntool.github.io/app/kntool.xml");
    xmlHttp.send();
  },
  mounted() {
    this.hello = "11";

  },
  methods: {
    SerializeRoot: function(xml) {
      var layouts = xml.getElementsByTagName("layout");

      if((layouts != null) && (layouts.length > 0)) {
        for(var i = 0; i < layouts.length; i++) {
          var pages = layouts[i].getElementsByTagName("page");
          if((pages != null) && (pages.length > 0)) {
            for(var j = 0; j < pages.length; j++) {
              //for(var j = 0; j < 1; j++) {
              this.SerializePage(pages[j]);
            }
          }
          try {
            this.showPage = layouts[0].getAttribute("topPage");
          } catch {}
        }

        var infomations = xml.getElementsByTagName("infomation");
        if((infomations != null) && (infomations.length > 0)) {
          this.app.title = infomations[0].getAttribute("title");
          this.app.version = infomations[0].getAttribute("version");
        }

        this.hello = this.app.title + " - Ver." + this.app.version;
      }
    },
    SerializePage(node) {
      var id = null;
      var backcolor = null;
      var type = 0;

      var museLabels = Array();
      var museTextFields = Array();
      var museSelectBoxs = Array();
      var museAction = null;

      if(node.getAttribute("id") != null) {
        id = node.getAttribute("id");
      }
      if(node.getAttribute("backcolor") != null) {
        backcolor = "#" + node.getAttribute("backcolor").substring(2, 8);
      }
      if(node.getAttribute("type") != null) {
        type = parseInt(node.getAttribute("type"));
      }

      var labels = node.getElementsByTagName("label");
      if((labels != null) && (labels.length > 0)) {
        for(var n = 0; n < labels.length; n++) {
          museLabels.push(this.SerializeLabel(labels[n]));
        }
      }

      var textFields = node.getElementsByTagName("textFiled");
      if((textFields != null) && (textFields.length > 0)) {
        for(var n = 0; n < textFields.length; n++) {
          museTextFields.push(this.SerializeTextField(textFields[n]));
        }
      }

      var selectboxs = node.getElementsByTagName("selectBox");
      if((selectboxs != null) && (selectboxs.length > 0)) {
        for(var n = 0; n < selectboxs.length; n++) {
          museSelectBoxs.push(this.SerializeSelectBox(selectboxs[n]));
        }
      }

      var actions = node.getElementsByTagName("action");
      if((actions != null) && (actions.length > 0)) {
        museAction = this.SerializeAction(actions[0]);
      }

      this.app.page.push({
        id: id,
        backcolor: backcolor,
        type: type, 
        label: museLabels,
        textfield: museTextFields,
        selectbox: museSelectBoxs,
        action: museAction,
      });

    },
    SerializeLabel(node) {
      var id = null;
      var x = 0;
      var y = 0;
      var width = 0;
      var height = 0;
      var system = 0;
      var common = "";
      var text = "";
      var textsize = 12;
      var textcolor = "FFFFFF";
      var valign = "left";
      var halign = "top";
      var textstyle = 0;
      var backcolor = "transparent";
      var bordercolor = "transparent"
      var borderwidth = 0;
      var borderstyle = "";

      try {
        id = node.getAttribute("id");
      } catch {}
      try {
        x = parseInt(node.getAttribute("x")); 
      } catch {}
      try {
        y = parseInt(node.getAttribute("y"));
      } catch {}
      try {
        width = parseInt(node.getAttribute("width"));
      } catch {}
      try {
        height = parseInt(node.getAttribute("height"));
      } catch {}
      try {
        system = parseInt(node.getAttribute("system"));
      } catch {}
      try {
        switch(system) {
          case 0:
            text = node.getAttribute("text");
            break;
          case 5:
            text = "Ver.x.x.x.x";
            break;
        }
      } catch {}
      try {
        textsize = parseInt(node.getAttribute("textsize"));
      } catch {}
      try {
        textcolor = "#" + node.getAttribute("textcolor").substring(2, 8);
      } catch {}
      try {
        backcolor = "#" + node.getAttribute("backcolor").substring(2, 8);
      } catch {}
      try {
        switch(parseInt(node.getAttribute("textalign")))
        {
          case 0x1:
            valign = "left";
            halign = "top";
            break;
          case 0x2:
            valign = "center";
            halign = "top";
            break;
          case 0x4:
            valign = "right";
            halign = "top";
            break;
          case 0x10:
            valign = "left";
            halign = "middle";
            break;
          case 0x20:
            valign = "center";
            halign = "middle";
            break;
          case 0x40:
            valign = "right";
            halign = "middle";
            break;
          case 0x100:
            valign = "left";
            halign = "bottom";
            break;
          case 0x200:
            valign = "center";
            halign = "bottom";
            break;
          case 0x400:
            valign = "right";
            halign = "bottom";
            break;
        }
      } catch {}
      try {
        switch(parseInt(node.getAttribute("borderstyle")))
        {
          case 1:
            borderstyle = "solid";
            break;
        }
      } catch {}
      try {
        borderwidth = parseInt(node.getAttribute("borderwidth"));
      } catch {}
      try {
        bordercolor = "#" + node.getAttribute("bordercolor").substring(2, 8);
      } catch {}

      return {
        id: id,
        x: x,
        y: y,
        width: width,
        height: height,
        text: text,
        textsize: textsize,
        textcolor: textcolor,
        backcolor: backcolor,
        valign: valign,
        halign: halign,
        borderstyle: borderstyle,
        borderwidth: borderwidth,
        bordercolor: bordercolor,
      };
    },
    SerializeTextField(node) {
      var id = null;
      var label = "";
      var x = 0;
      var y = 0;
      var width = 0;
      var height = 0;
      var activecolor = "black";

      try {
        id = node.getAttribute("id");
      } catch {}
      try {
        label = node.getAttribute("label");
      } catch {}
      try {
        x = parseInt(node.getAttribute("x")); 
      } catch {}
      try {
        y = parseInt(node.getAttribute("y"));
      } catch {}
      try {
        width = parseInt(node.getAttribute("width"));
      } catch {}
      try {
        height = parseInt(node.getAttribute("height"));
      } catch {}
      try {
        activecolor = "#" + node.getAttribute("activebackcolor").substring(2, 8);
      } catch {}

      return {
        id: id,
        label: label,
        x: x,
        y: y,
        width: width,
        height: height,
        activecolor: activecolor,
      };
    },
    SerializeSelectBox(node) {
      var id = null;
      var label = "";
      var x = 0;
      var y = 0;
      var width = 0;
      var height = 0;
      var activecolor = "black";

      try {
        id = node.getAttribute("id");
      } catch {}
      try {
        label = node.getAttribute("label");
      } catch {}
      try {
        x = parseInt(node.getAttribute("x")); 
      } catch {}
      try {
        y = parseInt(node.getAttribute("y"));
      } catch {}
      try {
        width = parseInt(node.getAttribute("width"));
      } catch {}
      try {
        height = parseInt(node.getAttribute("height"));
      } catch {}
      try {
        activecolor = "#" + node.getAttribute("activebackcolor").substring(2, 8);
      } catch {}

      return {
        id: id,
        label: label,
        x: x,
        y: y,
        width: width,
        height: height,
        activecolor: activecolor,
      };
    },

    SerializeAction(node) {
      var museKeys = Array();
      
      var keys = node.getElementsByTagName("key");
      if((keys != null) && (keys.length > 0)) {
        for(var n = 0; n < keys.length; n++) {
          museKeys.push(this.SerializeKey(keys[n]));
        }
      }

      return {
        key: museKeys,
      };
    },

    SerializeKey(node) {
      var vk = 0;
      var action = 0;
      var text = "";
      var textsize = 12;
      var color = "Red";
      var show = false;
      var width = 49;
      var x = 0;

      try {
        vk = parseInt(node.getAttribute("vk"));
      } catch {}
      try {
        action = node.getAttribute("action");
      } catch {}

      switch(vk) {
        case 132:
          color = "#FF2020";
          x = 7;
          break;
        case 112:
          color = "#2010FF";
          x = 66;
          break;
        case 113:
          color = "#10C010";
          x = 125;
          break;
        case 133:
          color = "#FFFF00";
          x = 184;
          break;
      }

      var label = node.getElementsByTagName("keyLabel");
      if((label != null) && (label.length > 0)) {
        try {
          text = label[0].getAttribute("text");
        } catch {}
        try {
          textsize = parseInt(label[0].getAttribute("textsize"));
        } catch {}
        try {
          if(label[0].getAttribute("hide") == "0") {
            show = true;
          }
        }catch {}
      }

      var success = null;
      var fail = null;

      var result = node.getElementsByTagName("result");
      if((result != null) && (result.length > 0)) {
        var s = result[0].getElementsByTagName("success");
        if((s != null) && (s.length > 0)) {
          success = this.SerializeResult(s[0]);
        }
        var f = result[0].getElementsByTagName("fail");
        if((f != null) && (f.length > 0)) {
          fail = this.SerializeResult(f[0]);
        }
      }

      return {
        vk: vk,
        action: action,
        text: text,
        textsize: textsize,
        color: color,
        show: show,
        width: width,
        x: x,
        success: success,
        fail: fail,
      };
    },
    SerializeResult(node) {
      var page = null;
      var buzzer = null;
      var error = false;

      try {
        page = node.getAttribute("page");
      } catch {}
      try {
        buzzer = node.getAttribute("buzzer");
      } catch {}
      try {
        error = node.getAttribute("error");
      } catch {}

      return {
        page: page,
        buzzer: buzzer,
        error: error,
      };
    },

    changePage(pageName) {
      this.showPage = pageName;
    },

    onLoadKaml: function(e) {
        if(window.File && window.FileReader && window.FileList && window.Blob) {
              var fileData = e.target.files[0];
              var reader = new FileReader();
              var me = this;

              reader.onload = function() {
                  var doc = reader.result;

                  var parser = new DOMParser();
                  var xml = parser.parseFromString(doc, "application/xml");

                  me.SerializeRoot(xml);
              };
              reader.readAsText(fileData, 'UTF-8');
          }
      },
  },
  computed :{
    showPages: function() {
      var pages = new Array();
      if((this.app != null) && (this.app.page !=null) && (this.app.page.length > 0)) {
        for(var n = 0; n < this.app.page.length; n++) {
          if(this.app.page[n].id == this.showPage) {
            pages.push(this.app.page[n]);
          }
        }
      }

      return pages;
    },
  },
  data: () => ({
    //
    hello: "",
    showPage: "",
    app: {
      title: "",
      version: "",
      showPage: "",
      page: [],
    },
  }),
};
</script>
